import { Tabs, Table } from 'antd';
const { TabPane } = Tabs;

function ServicesTable(props) {
    const dataSource = [
        {
          key: '1',
          donationType: 'Donations',
          amount: 100
        },
        {
          key: '2',
          donationType: 'Donations ex',
          amount: 50
        },
        {
          key: '3',
          donationType: 'Donations qw',
          amount: 150
        }
      ];
    const onTabChange = (tab, event) => {
        console.log('Test', tab, event);
    };

    const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
            console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
            props.onCheckboxChange(selectedRows, selectedRowKeys);
        }
    };

    return (
        <Tabs onTabClick={onTabChange} type="card">
            {props.tabsArr && props.tabsArr.map((rec, index) => {
                return <TabPane tab={rec.type} key={index}>
                    Content of {rec.type}
                    <Table 
                        dataSource={dataSource} 
                        columns={props.columns}  
                        pagination={false} 
                        rowSelection={{
                            type: 'checkbox',
                            ...rowSelection,
                        }} 
                    >

        </Table>
                </TabPane>
            })}
        </Tabs>
    )
}
export default ServicesTable;