import './App.css';
import 'antd/dist/antd.css';
import Header from './header/Header';
import ServicesTable from './table/ServicesTable';
import { Input, Form, Button, Modal } from 'antd';
import { ShoppingCartOutlined } from '@ant-design/icons';
import { useState } from 'react';
import Table from 'rc-table';

function App() {
  const columns = [
    {
      title: 'Service Name',
      dataIndex: 'donationType',
      key: 'donationType'
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
      key: 'amount'
    }
  ];
  const [modal2Visible, setModal2Visible] = useState(false);
  const [cartItems, setCartItems] = useState([]);
  const donationTypes = [
    {
      type: 'Donation 1',
    },
    {
      type: 'Donation 2',
    },
    {
      type: 'Donation 3',
    },
    {
      type: 'Donation 4',
    },
    {
      type: 'Donation 5',
    }];

    const onCheckboxChange = (selectedRows, selectedRowKeys) => {
      console.log(selectedRows);
      setCartItems([...cartItems, ...selectedRows]);
    };

  return (
    <div className="App">
      <Header></Header>
      <div className="h-center donations-title">Donations</div>
      <Form layout="vertical">
        <div className="h-center-inputs">
          <Form.Item style={{ marginRight: 20 }} label="Enter EMAIL">
            <Input name="customerEmail" type="email" />
          </Form.Item>
          <Form.Item label="Enter PHONE">
            <Input name="customerPhone" type="text" />
          </Form.Item>
        </div>
        <fieldset>
          <legend>Donar Profile</legend>
          <div className="h-center-inputs">
            <Form.Item style={{ marginRight: 20 }} label="Name">
              <Input name="customerEmail" type="email" />
            </Form.Item>
            <Form.Item style={{ marginRight: 20 }} label="Day">
              <Input name="customerDob" type="text" />
            </Form.Item>
            <Form.Item style={{ marginRight: 20 }} label="Gender">
              <Input name="customerGender" type="text" />
            </Form.Item>
            <Form.Item style={{ marginRight: 20 }} label="Gotra">
              <Input name="customerGotra" type="text" />
            </Form.Item>
            <Form.Item style={{ marginRight: 20 }} label="Nakshatra">
              <Input name="customerNakshatra" type="text" />
            </Form.Item>
          </div>
          <div className="h-center-inputs">
            <Form.Item style={{ marginRight: 20 }} label="Address">
              <Input name="customerAddress" />
            </Form.Item>
            <Form.Item style={{ marginRight: 20 }} label="City">
              <Input name="customerCity" type="text" />
            </Form.Item>
            <Form.Item style={{ marginRight: 20 }} label="State">
              <Input name="customerState" type="text" />
            </Form.Item>
            <Form.Item label="Zip">
              <Input name="customerZip" type="text" />
            </Form.Item>
          </div>
        </fieldset>
        <ServicesTable onCheckboxChange={(rows, keys)=>onCheckboxChange(rows, keys)} columns={columns} tabsArr={donationTypes}></ServicesTable>
        <div className="h-center-inputs">
          <Button type="primary" onClick={() => setModal2Visible(true)} style={{ margin: 20 }} className="btn-cls" icon={<ShoppingCartOutlined />} size="large">
            Cart
          </Button>
          <Button type="primary" className="btn-cls" style={{ margin: 20 }} size="large">
            Checkout
          </Button>
        </div>
      </Form>
      <Modal
        title="Cart Items"
        centered
        footer={[
          <Button key="submit" type="primary" onClick={() => setModal2Visible(false)}>
            OK
          </Button>,
        ]}
        visible={modal2Visible}
      >
        <Table dataSource={cartItems} columns={columns}></Table>
      </Modal>
    </div>
  );
}

export default App;
